/* ส่วนของการจัดการ container */
.container {
  display: flex; /* ใช้ flexbox เพื่อแบ่งหน้าจอ */
  height: 100vh; /* ให้สูงเต็มหน้าจอ */
}

/* ฝั่งซ้ายมีภาพพื้นหลัง */
.leftSide {
  flex: 0.7;
  background-image: url(data:image/jpeg;base64,); /* URL ของภาพพื้นหลัง */
  background-size: cover;
  background-position: center;
}

/* ฝั่งขวาจะเป็นฟอร์มล็อกอิน */
.rightSide {
  flex: 0.3;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
}

/* ฟอร์มล็อกอิน */
.form {
  background-color: transparent;
  padding: 30px 50px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

/* ปุ่มสำหรับล็อกอิน */
.buttonStyle {
  background-color: #F7D007;
  color: white;
  border: none;
  padding: 15px;
  width: 100%;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* การจัดตำแหน่งของโลโก้ Google */
.iconStyle {
  width: 20px;
  height: 20px;
  margin-right: 10px; /* ช่องว่างระหว่างโลโก้และข้อความ */
}
/* ===================== Responsive ===================== */

/* ขนาดหน้าจอเล็ก (แท็บเล็ตลงไป) */
@media (max-width: 768px) {
  .container {
    flex-direction: column; /* ซ้อนกันแทนแบบแนวนอน */
  }

  .leftSide {
    flex: none;
    height: 200px;
    width: 100%;
    background-position: top;
  }

  .rightSide {
    flex: none;
    width: 100%;
    padding: 20px;
  }

  .form {
    padding: 20px;
    box-shadow: none;
    max-width: 100%;
  }

  .buttonStyle {
    font-size: 15px;
    padding: 12px;
  }

  .iconStyle {
    width: 18px;
    height: 18px;
  }
}

/* หน้าจอมือถือเล็กมาก */
@media (max-width: 480px) {
  .form h2 {
    font-size: 20px;
  }

  .buttonStyle {
    font-size: 14px;
    padding: 10px;
  }

  .iconStyle {
    width: 16px;
    height: 16px;
  }
}
